.dot-btn:before {
    content: "";
    height: 9px;
    width: 9px;
    position: absolute;
    top: 5px;
    right: 1px;
    border-radius: 100%;
    z-index: 1;
    border: 1px solid #fff
}

.dot-btn.grow:before {
    border: none;
    height: 7px;
    width: 7px
}

.dot-btn.grow:after {
    content: "";
    height: 19px;
    width: 18px;
    display: inline-block;
    border-radius: 100%;
    top: -1px;
    right: -4.2px;
    position: absolute
}

