.button-effect {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    overflow: hidden;
    position: relative
}

.button-effect:hover {
    cursor: pointer
}

.effect-wave {
    display: block;
    position: absolute;
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
    width: 0;
    height: 0;
    background-color: #eff1f2;
    border-radius: 50%;
    opacity: 0.5
}

.is-animating {
    -webkit-transform: translate3d(0, 0, 0) scale(1);
    transform: translate3d(0, 0, 0) scale(1)
}

.is-animating.btn-success {
    -webkit-animation: phone-outer-success 3000ms infinite;
    animation: phone-outer-success 3000ms infinite
}

.is-animating.btn-danger {
    -webkit-animation: phone-outer-danger 3000ms infinite;
    animation: phone-outer-danger 3000ms infinite
}

.is-animating.btn-danger svg {
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
    -webkit-animation: none;
    animation: none
}

.is-animating::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 100%;
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0) scale(0);
    transform: translate3d(0, 0, 0) scale(0);
    -webkit-animation: phone-inner 3000ms infinite;
    animation: phone-inner 3000ms infinite
}

.is-animating svg {
    -webkit-animation: phone-icon 3000ms infinite;
    animation: phone-icon 3000ms infinite;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    fill: white;
    stroke-width: 1px
}

.is-animating.cancelcall {
    -webkit-animation: none;
    animation: none
}

.is-animating.cancelcall::before {
    -webkit-animation: none;
    animation: none
}

